/* SliderBox.css */
.slider-box {
    width: 100%;
    text-align: center;
  }
  
  .slider {
    width: 100%;
    height: 20px;
    background: #ccc;
    position: relative;
  }
  
  .slider-bar {
    height: 100%;
    transition: width 0.3s ease-in-out;
  }
  
  /* Define the zone colors */
  .slider-bar {
    background: red; /* Bad zone */
  }
  
  .slider-bar.yellow {
    background: yellow; /* Medium zone */
  }
  
  .slider-bar.green {
    background: green; /* Good zone */
  }
  